.survey {
    width: 100vw;
    height: calc(100vh - 91px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.form {
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.controls {
    position: fixed;
}

@media screen and (min-width: 800px) {
    .container {
        flex-direction: row;
        justify-content: space-between;
    }
}

.outlined {
    border-color: white;
}

.input {
    color: white;
}