.current {
    transition: transform 600ms ease 0s, opacity 600ms ease 0s;
}

.previous {
    transition: transform 600ms ease 0s, opacity 600ms ease 0s;
    opacity: 0.5;
    cursor: pointer;
}

.upcoming {
    transition: transform 600ms ease 0s, opacity 600ms ease 0s;
    opacity: 0;
    transform: translateY(30vh);
    height: 0;
}

.animating {
    display: inherit;
}

.card {
    margin: 10px 0;
}

.topMargin {
    margin-top: 20px !important;
}
